@import url(https://fonts.googleapis.com/css2?family=Spartan:wght@500;700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body, html {
    font-family: 'Spartan', sans-serif;
    background: hsl(180, 52%, 96%);
    -webkit-font-smoothing: antialised;
    width: 100%;
    height: 100%;
}

button {
    font-family: 'Spartan', sans-serif;
}

header {
    background: hsl(180, 29%, 50%);
    height: 150px;
}

header img {
    width: 100%;
    height: 100%;
}


.list-container {
    padding: 80px 15%;
    width: 100%;
    /* background: black; */
}
.card {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: white;
    margin-bottom: 25px;
    padding: 20px 25px;
    border-radius: 8px;
    box-shadow: 0px 10px 5px hsl(180, 29%, 50%, 0.1);
}

.border {
    border-left: solid 5px hsl(180, 29%, 50%);
}

.card div {
    display: flex;
    align-items: center;
}

.card .desc {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    margin-left: 25px;
}

.card button {
    background: hsl(180, 31%, 95%);
    border: 0;
    border-radius: 3px;
    padding: 7px;
    color: hsl(180, 29%, 50%);
    font-weight: bold;
    font-size: 12px;
    margin: 0 5px;
    cursor: pointer;
}

.card button:hover {
    background: hsl(180, 29%, 50%);
    color: white;
}

.group {
    display: flex;
    align-items: center;
    margin-bottom: 10px;
}

h5 + .group {
    margin-bottom: 0;
    margin-top: 10px;
}

.group label {
    margin-right: 10px;
    padding: 5px 10px;
    border-radius: 15px;
    color: white;
    font-size: 10px;
    font-weight: bold;
    display: flex;
    align-items: center;
}

.new {
    background: hsl(180, 29%, 50%);
}

.featured {
    background: hsl(180, 14%, 20%);
}

.desc h5 {
    color: hsl(180, 14%, 20%);
    font-size: 16px;
    cursor: pointer;
    transition: color 0.2s;
}

.desc h5:hover {
    color: hsl(180, 29%, 50%);
}

.group h4 {
    color: hsl(180, 29%, 50%);
    font-size: 14px;
    margin-right: 20px;
}

.group p {
    color: hsl(180, 8%, 52%);
    font-size: 12px;
    margin-right: 15px;
}
.filter {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: absolute;
    background: white;
    width: 70%;
    top: 120px;
    left: 15%;
    right: 15%;
    /* margin: 0 15%; */
    border-radius: 8px;
    box-shadow: 0px 10px 5px hsl(180, 29%, 50%, 0.1);
    padding: 20px 25px;
}

.hide-display {
    display: none;
}

.filter div {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
}

.filter div .button {
    height: 26px;
    background: hsl(180, 31%, 95%);
    display: flex;
    align-items: center;
    border-radius: 3px;
    color: hsl(180, 29%, 50%);
    font-weight: bold;
    font-size: 12px;
    text-decoration: none;
    padding-left: 7px;
    cursor: default;
    margin-right: 10px;
    margin-bottom: 5px;
}

.button span {
    width: 26px;
    height: 26px;
    background-color: hsl(180, 29%, 50%);
    border-top-right-radius: 3px;
    border-bottom-right-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-left: 7px;
    cursor: pointer;
}

.button span:hover {
    background-color: hsl(180, 14%, 20%);
}

/* .button span::after {
    content: '';
    background-image: url('./icon-remove.svg');

} */

div + label {
    color: hsl(180, 29%, 50%);
    text-decoration: none;
    font-size: 12px;
    font-weight: bold;
    cursor: pointer;
}

div + label:hover {
    text-decoration: underline;
}
